@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

/* Reset and base styles  */
* {
    box-sizing: border-box;
	padding: 0px;
	margin: 0px;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

/* Links */

a, a:link, a:visited  {
    text-decoration: none;
}

a:hover  {
    text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */

input, textarea, button, select {
	font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
    outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}

:root {
    --main-color: #0079FF;
    --main-color-hover: #60ABFF;
  
    --white: #FFF;
  
    --gray-50: #F6F8FF;
    --gray-100: #697C9A;
    --gray-200: #4B6A9B;
    --gray-300: #2B3442;
    --gray-400: #1E2A47;
    --gray-500: #141D2F;
  
    --error-color: #F74646;
  
    --shadow: 0px 16px 30px -10px rgba(70, 96, 187, 0.198567);
  }

body {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: var(--gray-50);
}
  
body[data-theme='dark'] {
    background-color: var(--gray-500);
}
  
a {
    font-family: 'Poppins', sans-serif;
}

.container {
    width: 100%;
    padding: 0 24px;
  
    @media (min-width: 767px) {
        width: 734px;
        padding: 0;
        margin: 0 auto;
    }
}